.btn {
    outline: none !important;

    &.btn-icon {
        height: $icon-size-regular;
        min-width: $icon-size-regular;
        width: $icon-size-regular;
        padding: 0;
        font-size: $icon-font-size-regular;
        overflow: hidden;
        position: relative;
        line-height: normal;

        &.btn-simple {
            padding: 0;
        }

        &.btn-sm {
            height: $icon-size-sm;
            min-width: $icon-size-sm;
            width: $icon-size-sm;

            i {
                font-size: $icon-font-size-sm;
            }
        }

        &.btn-lg {
            height: $icon-size-lg;
            min-width: $icon-size-lg;
            width: $icon-size-lg;

            i {
                font-size: $icon-font-size-lg;
            }
        }

        &:not(.btn-footer) {
            i {
                position: absolute;
                font-size: 1em;
                top: 50%;
                left: 50%;
                transform: translate(-12px, -12px);
                line-height: 1.5626rem;
                width: 24px;
            }
        }
    }
}

@each $color, $value in $theme-colors {
    .btn-#{$color} {
        @include button-variant($value, $value);
    }
}

@each $color, $value in $theme-colors {
    .btn-outline-#{$color} {
        @include button-outline-variant($value);
    }
}

@each $color, $value in $main-colors {
    .btn-#{$color} {
        @include button-gradient(
            $value,
            map-get($states-colors, "#{$color}-states")
        );
    }
}

