.btn {
    &:disabled,
    &[disabled],
    &.disabled {
        @include opacity(0.5);

        pointer-events: none;
    }
}

.btn-simple {
    border: $border;
    border-color: $default;
    box-shadow: none;
    padding: ($padding-btn-vertical - 1) ($padding-round-horizontal - 1);
    background-color: transparent;
}

.btn-simple,
.btn-link {
    &.disabled,
    &:disabled,
    &[disabled],
    fieldset[disabled] & {
        &,
        &:hover,
        &:focus,
        &.focus,
        &:active,
        &.active {
            background: transparent;
        }
    }
}

// .btn:not(:disabled):not(.disabled):active, .btn:not(:disabled):not(.disabled).active {
//     box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
// }

.btn-link {
    border: $none;
    padding: $padding-base-vertical $padding-base-horizontal;
    background: transparent;
    color: $gray-600;
    font-weight: $font-weight-bold;
    outline: none !important;
    text-decoration: none;

    &:hover {
        color: lighten($gray-600, 20%);
    }
}

.btn-wd {
    min-width: 140px;
}

.btn-group.select {
    width: 100%;
}

.btn-group {
    .btn.active {
        box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
        transform: translateY(-1px);
    }

    label.btn.active {
        transform: translateY(0);
    }
}

.btn-group.select .btn {
    text-align: left;
}

.btn-group.select .caret {
    position: absolute;
    top: 50%;
    margin-top: -1px;
    right: 8px;
}

.btn-round {
    border-width: $border-thin;
    border-radius: $btn-round-radius;

    &.btn-simple {
        padding: ($padding-btn-vertical - 1) ($padding-round-horizontal - 1);
    }
}

.no-caret {
    &.dropdown-toggle::after {
        display: none;
    }
}

.btn {
    // social buttons
    &.btn-facebook {
        @include social-buttons-color($facebook, $facebook-states);
    }

    &.btn-twitter {
        @include social-buttons-color($twitter, $twitter-states);
    }

    &.btn-pinterest {
        @include social-buttons-color($pinterest, $pinterest-states);
    }

    &.btn-google {
        @include social-buttons-color($google-plus, $google-plus-states);
    }

    &.btn-linkedin {
        @include social-buttons-color($linkedin, $linkedin-states);
    }

    &.btn-dribbble {
        @include social-buttons-color($dribbble, $dribbble-states);
    }

    &.btn-github {
        @include social-buttons-color($github, $github-states);
    }

    &.btn-youtube {
        @include social-buttons-color($youtube, $youtube-states);
    }

    &.btn-instagram {
        @include social-buttons-color($instagram, $instagram-states);
    }

    &.btn-reddit {
        @include social-buttons-color($reddit, $reddit-states);
    }

    &.btn-tumblr {
        @include social-buttons-color($tumblr, $tumblr-states);
    }

    &.btn-behance {
        @include social-buttons-color($behance, $behance-states);
    }

    &.btn-double-padding {
        $double-padding: $btn-padding-x * 2;
        padding-left: $double-padding;
        padding-right: $double-padding;

        &.btn-sm {
            $double-padding-sm: $btn-padding-x-sm * 2;
            padding-left: $double-padding-sm;
            padding-right: $double-padding-sm;
        }

        &.btn-lg {
            $double-padding-lg: $btn-padding-x-lg * 2;
            padding-left: $double-padding-lg;
            padding-right: $double-padding-lg;
        }
    }
}

.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
    color: $white;
}

@include media-breakpoint-down(md) {
    .btn-group .btn {
        padding-left: $padding-round-horizontal;
        padding-right: $padding-round-horizontal;
    }
}
